export function authorization_status(trello, options) {
	console.log("Running authorization status")
	return trello.get("member", "private", "access_token")
		.then(console.log)
		.then(x => {
			return {authorized: false};
		})
}

export function show_authorization(trello, options) {
	return trello.popup({
		title: "My Auth Popup",
		url: "https://www.goodreads.com",
		height: 140,
	})
}